html, body {
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden; /* 스크롤바 제거 */
}

.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw; /* 화면 너비 꽉 채우기 */
    height: 100vh; /* 화면 높이 꽉 채우기 */
    background-color: rgba(0, 0, 0, 0.7);
    padding: 20px;
}

.login-title {
    font-size: 24px;
    font-weight: bold;
    color: white;
    margin-bottom: 30px;
}

button {
    background-color: white;
    border: none;
    border-radius: 10px;
    padding: 15px 40px;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 15px;
    width: 300px;
    height: 60px;
    cursor: pointer;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

button:hover {
    background-color: #f1f1f1;
}

.google-login-button {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 18px;
    font-weight: bold;
}

.google-login-button img {
    margin-right: 10px;
    width: 24px;
    height: 24px;
}

.logout-button {
    background-color: #f44336;
    color: white;
}

.logout-button:hover {
    background-color: #e57373;
}